.home-expbullets-container{
    margin-top: 15px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}
.home-expbullets-inner-container{
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
}
.home-expbullets-list{
    padding-block: 45px;
    background-color: white;
    padding-inline: 30px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}
.home-expbullets-list:not(:last-child){
    margin-right: 30px;
}

.home-expbullets-subheading{
    margin-top: 75px;
    text-align: left;
}
.home-expbullets-text{
    padding-block: 5px;
    text-align: left;
}
.home-expbullets-link{
    margin-top: 30px;
    text-decoration: none;
}
.home-expbullets-link:hover{
    text-decoration: underline;
}
.home-expbullets-img-container{
    height: 60px;
    overflow: hidden;
}

.home-expbullets-img-container img{
    height: 100%;
}
.home-expbullets-list:hover{
    box-shadow: 0px 0px 10px #002842;
}


@media (max-width: 1000px) {
    .home-expbullets-list:not(:last-child){
        margin-right: 0px;
        margin-bottom: 30px;
    }
    .home-expbullets-inner-container{
        flex-direction: column;
    }   
}

