.home-about-container{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-block: 90px;
}
.home-about-inner-container{
    width: 900px;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
}
.home-about-para{
    margin-block: 30px;
    
}
.home-about-link{
    display: flex;
    align-self: flex-end;
    color: #002842;
    text-decoration: none;
}
.home-about-link:hover{
    text-decoration: underline;
}
.h1-heading{
    font-size: 24px;
    font-weight: 500;
    color: #002842;

}

.h2-heading{
    font-size: 18px;
    font-weight: 600;
}
.normal-text{
    font-size: 15px;
    text-align: justify;
}
.normal-text-link{
    font-size: 15px;
    font-weight: 600;
    color:#0062A2;
    text-align: justify;
}



@media (max-width: 1000px) {
    .home-about-inner-container{
        width:90%
    }

}